import React, { useState, useEffect } from 'react';
import { Container, Form, Button, Spinner, Row, Col } from 'react-bootstrap';
import { fetchCsrfToken } from '../../services/api/fetchCsrfService';
import {  callwebService } from "../../utils/helpers";
import useToast from "../../hooks/useToast"; 
import { MSG_ROUTE_TEMPLATE_CREATE_SUCCESS } from "../../utils/toastMessages";

import { ReactTabulator } from 'react-tabulator'; // Import ReactTabulator
import 'react-tabulator/lib/css/tabulator.css';  // Import Tabulator styles


const ChangeCollabSpace = () => {
  const { showSuccessToast, showErrorToast } = useToast();
  const [sourceCollabSpace, setSourceCollabSpace] = useState('');
  const [targetCollabSpace, setTargetCollabSpace] = useState('');
  const [type, setType] = useState('');
  const [data, setData] = useState([]);
  const [isLoading, setIsLoading] = useState(false);
  const [sourceOrg, setSourceOrg] = useState([]);
  const [sourceOrgValue, setSourceOrgValue] = useState([]);
  const [targetOrgValue, setTargetOrgValue] = useState([]);
  const [targetOrg, setTargetOrg] = useState([]);
  const [updateVal, setUpdateVal] = useState({
    "collabspace": "",
    "owner": "",
     "organization": "",
   
        "data": [ ] 
   })
 
  const collabSpaceOptions = [
    "ACT-Admins",
    "ACT-Biffi",
    "ACT-Electric",
    "ACT-Electric Nuclear",
    "ACT-Gear",
    "ACT-Hydraulic",
    "ACT-NPD Process",
    "ACT-Pneumatic",
    "ACT-Pneumatic Nuclear",
    "ACT-Rack And Pinion",
    "ACT-Scotch Yoke",
    "ACT-Scotch Yoke Nuclear",
    "ACT-Gas Motors",
    "Actuation",
    "Common Space",
    "DummyTest",
    "ISV-Ball Valves",
    "ISV-Butterfly Valves",
    "JDE Permasense",
    "MSOL",
    "MSOL-Commercial Twin",
    "MSOL-Corosion Erosion",
    "MSOL-Corrosion Erosion Libraries",
    "MSOL-Cross Portfolio",
    "MSOL- DP Flow",
    "MSOL-DP Flow",
    "MSOL-DP Level",
    "MSOL-Flexim",
    "MSOL-Flexim6B8F27BD52F6220067BC465F000007FF",
    "MSOL-Mag & Vortex",
    "MSOL-Mags and Vortex",
    "Micro Motion",
    "MSOL-Micro Motion Libraries",
    "MSOL-Nuclear High Integrity",
    "MSOL-Pressure Temperature",
    "MSOL-Process Level",
    "Rosemount Measurement",
    "MSOL-Roxar",
    "MSOL-System Acceptance",
    "Ultrasonics",
    "MSOL-Wireless",
    "MSOLTest",
    "PRM-Tank",
    "Rosemount Flow",
    "Rosemount Level",
    "Roxar"
];
  const typeOptions = ['VPMReference', 'Document', 'Drawing'];

  const getOrgs = async (selectedCollab ,targetFuction )=>{
    const uri = `https://oi000186152-us1-space.3dexperience.3ds.com/enovia/resources/pno/collaborativespace/${encodeURIComponent(selectedCollab)}/organizations`;
    await callwebService("GET", uri, {})
    .then( async (webserviceresult) => {
      let result  = webserviceresult;
      console.log(result);        
        const orgTitles = result.output.organizations.map(org => ({
          id: org.pid,
          title: org.title,
          name: org.name
      }));
      targetFuction(orgTitles);
      
      });
  }
 
  const handleSourceChange = async (event,targetFuction) => {
    const selectedValue = event.target.value;
    try {
    getOrgs(selectedValue,setSourceOrg)
   
    setSourceCollabSpace(selectedValue);
    setIsLoading(true);
    
    } catch (error) {
      console.error('Error fetching data:', error);
    } finally {
      setIsLoading(false);
    }
  };

  const handleTargetChange = (event) => {
    const selectedValue = event.target.value;
    
    try {
      getOrgs(selectedValue,setTargetOrg)
    setTargetCollabSpace(selectedValue)
    setIsLoading(true);
    } catch (error) {
      console.error('Error fetching data:', error);
    } finally {
      setIsLoading(false);
    }
  };

  const handleTypeChange = (event) => {
    setType(event.target.value);
  };

  const handleSourceOrgChange = (event) => {
    setSourceOrgValue({value : event.target.value, title : event.target.title});
  };

  const handleTargetOrgChange = (event) => {
    setTargetOrgValue({value : event.target.value, title : event.target.title});
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    createTemplate();
  };

  const handleUpdate = async (event)=>{
    event.preventDefault();
    setUpdateVal(prevUpdateVal => ({
      ...prevUpdateVal,
      collabspace: targetCollabSpace,
      organization: targetOrgValue.value
  }));
  await fetchCsrfToken(widget.getValue("Credentials"))
      .then(async (csrfToken) => {
        const headers = {
          "Content-Type": "application/json",
          "Accept": "application/json",
          ...csrfToken
        };
        const postdata = {
          "data": updateVal
        };
        await callwebService('POST', `https://oi000186152-us1-space.3dexperience.3ds.com/enovia/resources/v1/modeler/dslc/ownership/transfer`, JSON.stringify(postdata), headers)
          .then(() => {
            showSuccessToast(MSG_ROUTE_TEMPLATE_CREATE_SUCCESS);
          });
      });
    alert(updateVal)
    setUpdateVal({
      "collabspace": "",
      "owner": "",
       "organization": "",
     
          "data": [ ] 
     });
    alert("Hi")
  }
  
  const createTemplate = async () => {
    const dynamicQuery = `flattenedtaxonomies:"types/${type}" AND [ds6w:project]:"${sourceCollabSpace}" AND [ds6w:organizationResponsible]:"${sourceOrgValue.value}" `;

    await fetchCsrfToken(widget.getValue("Credentials"))
      .then(async (csrfToken) => {

        const data = {
          with_indexing_date: true,
          with_synthesis: true,
          with_nls: false,
          label: "3DSearch-punithkumar.m-AdvancedSearch-1742483517365",
          locale: "en",
          select_predicate: [
            "ds6w:label",
            "ds6w:type",
            "ds6w:description",
            "ds6w:identifier",         
            "ds6w:created",           
            "ds6w:responsible",
            "owner",           
            "ds6w:project",     
            "ds6w:originator",
            "dcterms:title",
            "dcterms:description"
          ],
          with_synthesis_hierarchical: false,
      
          query: dynamicQuery, 
          refine: {},
          specific_source_parameter: {
            "3dspace": {
              additional_query: ' AND NOT (owner:"ENOVIA_CLOUD" OR owner:"Service Creator" OR owner:"Corporate" OR owner:"User Agent" OR owner:"SLMInstallerAdmin" OR owner:"Creator" OR owner:"VPLMAdminUser")'
            }
          },
          select_exclude_synthesis: ["ds6w:what/ds6w:topic"],
          order_by: "desc",
          order_field: "relevance",
          select_snippets: [ ],
          nresults: 2,
          start: "0",
          source: ["3dspace"],
          tenant: "OI000186152",
          login: {
            "3dspace": {
              SecurityContext: "VPLMCreator.Company Name.Common Space"
            }
          }
        };

        fetch("https://oi000186152-us1-fedsearch.3dexperience.3ds.com/federated/search?xrequestedwith=xmlhttprequest", {
          headers: {
            accept: "application/json",
            "content-type": "application/json"
          },
          body: JSON.stringify(data),
          method: "POST",
          credentials: "include"
        })
          .then(response => response.json())
          .then(val => {
            const transformedData = val.results?.map(item => {
              setUpdateVal(prevUpdateVal => ({
                ...prevUpdateVal,
                data: [...prevUpdateVal.data, { id: item.attributes[0].value }]
            }));
              const newItem = {};
              item.attributes.forEach(attr => {
                // eslint-disable-next-line default-case
                switch (attr.name) {
                  case 'ds6w:label':
                    newItem.Title = attr.value;
                    break;
                
                  case 'owner':
                    newItem.owner = attr.value;
                    break;
                  case 'ds6w:description':
                    newItem.description = attr.value;
                    break;
                 
                  case 'ds6w:identifier':
                    newItem.name = attr.value;
                    break;
                }
              });
              return newItem;
            });
            setData(transformedData);
          })
          .catch(error => console.error('Error:', error));
      });
  };

  

  const filteredTargetOptions = collabSpaceOptions.filter(option => option !== sourceCollabSpace);
  return (
    <Container className="mt-4 scrollable-container">
      <h1>Change Ownership</h1>
      <Form onSubmit={handleSubmit}>
        <Form.Group controlId="sourceCollabSpace">
          <Form.Label>Source Collab-Space</Form.Label>
          <Form.Control as="select" value={sourceCollabSpace} onChange={handleSourceChange}>
            <option value="">Select Source Collab-Space</option>
            {collabSpaceOptions.map((option, index) => (
              <option key={index} value={option}>{option}</option>
            ))}
          </Form.Control>
        </Form.Group>
        <Form.Group controlId="targetCollabSpace">
          <Form.Label>Target Collab-Space</Form.Label>
          <Form.Control as="select" value={targetCollabSpace} onChange={handleTargetChange}>
            <option value="">Select Target Collab-Space</option>
            {filteredTargetOptions.map((option, index) => (
              <option key={index} value={option}>{option}</option>
            ))}
          </Form.Control>
        </Form.Group>
        <Form.Group controlId="sourceOrg">
          <Form.Label>Source Organization</Form.Label>
          <Form.Control as="select" value={sourceOrgValue.name} onChange={handleSourceOrgChange}>
            <option value="">Select Source Org</option>
            {sourceOrg.map((option, index) => (
              <option key={index} value={option.name}>{option.title}</option>
            ))}
          </Form.Control>
        </Form.Group>
        <Form.Group controlId="targetOrg">
          <Form.Label>Target Organization</Form.Label>
          <Form.Control as="select" value={targetOrgValue.name} onChange={handleTargetOrgChange}>
            <option value="">Select Target Org</option>
            {targetOrg.map((option, index) => (
              <option key={index} value={option.name}>{option.title}</option>
            ))}
          </Form.Control>
        </Form.Group>
        <Form.Group controlId="type">
          <Form.Label>Type</Form.Label>
          <Form.Control as="select" value={type} onChange={handleTypeChange}>
            <option value="">Select Type</option>
            {typeOptions.map((option, index) => (
              <option key={index} value={option}>{option}</option>
            ))}
          </Form.Control>
        </Form.Group>
        <Row className="mt-4">
          <Col className="d-flex justify-content-end">
            <Button variant="primary" type="submit">
              Submit
            </Button>
          </Col>
        </Row>
      </Form>
      {isLoading ? (
        <Spinner animation="border" />
      ) : (
        <div className="mt-4">
          <ReactTabulator
            data={data}
            columns={[
              { title: "Title", field: "Title"  },
              { title: "Name", field: "name"  },
              { title: "Description", field: "description" },
              { title: "Owner", field: "owner" },
            ]}
            options={{
              pagination: "local",  // Enable pagination
              paginationSize: 20,   // Number of rows per page
              layout: "fitData",    // Fit data within table width
            }}
          />
          <Row className="mt-4">
          <Col className="d-flex justify-content-end">
            <Button variant="primary" onClick={handleUpdate} >
              Update
            </Button>
          </Col>
        </Row>
        </div>
      )}
    </Container>
  );
};

export default ChangeCollabSpace;
